export function poiClusterStyle(size) {
    return [
        new Style({
            zIndex: 12,
            image: new RegularShape({
                fill: new Fill({
                    color: 'rgba(255, 165, 60, 0.9)'
                }),
                stroke: new Stroke({
                    color: '#fff'
                }),
                points: 3,
                radius: 20,
                angle: 45
            }),
            text: new Text({
                text: size.toString(),
                fill: new Fill({
                    color: '#fff'
                })
            })
        })
    ]
}

export function userLocationStyle() {
    return [
        new Style({
            image: new CircleStyle({
                radius: 6,
                fill: new Fill({
                    color: '#3399CC'
                }),
                stroke: new Stroke({
                    color: '#fff',
                    width: 2
                })
            })
        })
    ]
}





export function pinCircleDotStyle() {
    return [
        new Style({
            image: new Icon({
                color: [113, 140, 0],
                crossOrigin: 'anonymous',
                src: require('../assets/icons/dot.png')
            })
        })
    ]
}

let styleCache = {};

export function defaultClusterStyle(feature) {
    let size = feature.get('features').length;
    let style = styleCache[size];

    if (!style) {
        style = new Style({
            image: new CircleStyle({
                radius: 10,
                stroke: new Stroke({
                    color: '#fff'
                }),
                fill: new Fill({
                    color: '#3399CC'
                })
            }),
            text: new Text({
                text: size.toString(),
                fill: new Fill({
                    color: '#fff'
                })
            })
        });
    }

    return style
}

