export function featureAnimationStyle(radius, opacity) {
    return new Style({
        image: new CircleStyle({
            radius: radius,
            stroke: new Stroke({
                color: 'rgba(255, 0, 0, ' + opacity + ')',
                width: 0.25 + 1
            })
        })
    })
}

let styleCacheNeedHelp = {};
let styleCacheReadyAssist = {};

export function needHelpStyleBase(feature) {
    let size = feature.get('features').length
    let style = styleCacheNeedHelp[size];

    if (!style) {
        if (size > 1) {
            style = needHelpStyleCluster(size)
        } else {
            style = needHelpStyle()
        }
        styleCacheNeedHelp[size] = style
    }
    // else {
    // console.log('has style')
    // }

    return style
}

export function readyAssistStyleBase(feature) {
    let size = feature.get('features').length
    let style = styleCacheReadyAssist[size];

    if (!style) {
        if (size > 1) {
            style = readyAssistStyleCluster(size)
        } else {
            style = readyAssistStyle()
        }
        styleCacheReadyAssist[size] = style
    }

    return style
}

